<template>
	<div class="box">
    <div class="headline">
      <div class="go-back">
        <img src="../../public/img/detail-page03.png" alt>
        <span @click="upOne">返回</span>
      </div>
      <span class="book-details">搜索</span>
      <div class="homepage">
        <img src="../../public/img/detail-page02.png" @click="homepage">
      </div>
    </div>

		<div class="mainPart">

			<div class="seek">
				<div class="inputCase">
					<img src="../../public/img/Seek01.png">
					<input @input="inputText" ref="input" type="text" placeholder="请输入搜索内容">
				</div>
				<span @click="emptyInput">确定</span>
			</div>

			<!-- ? -->
			<div class="hint" v-show="hintText.length > 0">
				<div v-for="(item, index) in hintText" :key="index" @click="hintGoBook(item)">
					<img src="../../public/img/Seek03.png">
					<span>{{item}}</span>
				</div>
			</div>

			<div class="hotWord">
				<h3>搜索热词</h3>
				<span v-for="(item, index) in span1" :key="index" @click="goBook(item)">{{item}}</span>
			</div>

			<div class="hotWord">
				<h3>热门推荐</h3>
				<span v-for="(item, index) in span2" :key="index" @click="goBook(item)">{{item}}</span>
			</div>

			<div class="history">
				<div class="firstLine">
					<h3>搜索历史</h3>
					<div class="delete">
						<span @click="emptySpan">删除历史</span>
						<img src="../../public/img/Seek02.png">
					</div>
				</div>
				<span v-for="(item, index) in record" :key="index">{{item}}</span>
			</div>
			
		</div>

	</div>	
</template>

<script>
export default {
	name: "Seek",
	data() {
		return {
			span1: ['万界','网游','武炼巅峰','兵王','斗罗大陆','道君','英雄联盟','透视','医','万古仙穹','海贼王','全球高武','大主宰','辰东','变身'],
			span2: ['最佳赘婿','暖婚100分：总裁，轻点宠','盛世天骄','都挺好（精）','重生之都市仙尊','我的微信连三界'],
			hintText: "",
			record: [],
		}
	},
	methods: {
		upOne() {
      this.$router.push({path: '/'})
    },
		homepage() {
      this.$router.push({path: "/"})
		},
		inputText() {
			let hintUrl = "/api/book/auto-complete?query=" + this.$refs.input.value
			this.axios.get(hintUrl).then(
				response => {
					this.hintText = response.data.keywords
					// console.log(this.hintText)
				}
			)
		},
		emptyInput() {
			this.$router.push({path: "/RelatedBook", query: {name: this.$refs.input.value}})
			if(this.$refs.input.value !== "") {
				this.record.push(this.$refs.input.value) 
				this.$refs.input.value = ""
				this.hintText = ""
			}
			
		},
		emptySpan() {
			this.record = []
		},
		goBook(text) {
			this.$router.push({path: "/RelatedBook", query: {name: text}})
			// window.location.reload()
		},
		hintGoBook(text) {
			this.$router.push({path: "/RelatedBook", query: {name: text}})
			this.$refs.input.value = text
			this.record.push(text)
		}
	},
}
// http://api.zhuishushenqi.com/book/auto-complete?query=蛊真人
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@import "../../public/sass/mixin";
.box {
	margin-top: -100px;
  z-index: 999;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%;
  background: #fff;
}

.box .headline {
  display: flex;
  width: 100%;
  height: 44px;
  background: #b93321;

  .go-back {
    flex: 0 0 60px;
    width: 60px;
    height: 100%;
    line-height: 44px;

    img {
      margin-left: 10px;
      width: 15px;
    }

    span {
      display: inline-block;
      margin-top: -3px;
      vertical-align: top;
      font-size: 12px;
      font-weight: 400;
      color: #fff;
    }
  }

  .book-details {
    flex: 1;
    text-align: center;
    line-height: 44px;
    font-size: 14px;
    color: #fff;
  }

  .homepage {
    flex: 0 0 50px;
    width: 50px;
    height: 100%;
    text-align: center;
    line-height: 44px;

    img {
      width: 15px;
    }
  }
}

.box .mainPart {
	padding: 15px 15px 0px 15px;
	width: calc(100% - 30px);
}

.seek {
	display: flex;
	width: 100%;
	height: 56px;

	.inputCase {
		position: relative;
		padding: 0 44px;
		flex: 1;
		height: 35px;
		line-height: 35px;
		border-top-left-radius: 17px;
		border-top-right-radius: 17px;
		border-bottom-left-radius: 17px;
		border-bottom-right-radius: 17px;
		background: #ebebf0;

		img {
			position: absolute;
			top: 17.5px;
			left: 10px;
			display: inline-block;
			width: 24px;
			transform: translateY(-50%);
		}

		input {
			border: none;
			font-size: 12px;
			outline: none;
			color: #222;
			background: #ebebf0;
		}
	}

	span {
		flex: 0 0 30px;
		display: inline-block;
		margin-left: 10px;
		width: 30px;
		height: 35px;
		line-height: 35px;
		font-size: 15px;
		color: #748389;
	}
}

.hotWord {
	margin-bottom: 30px;
	width: 100%;

	h3 {
		margin-bottom: 14px;
		height: 30px;
		font-size: 22px;
		color: #6b6b6b;
	}

	span {
		display: inline-block;
		margin: 0 10px 10px 0;
		padding: 0 10px;
		height: 30px;
		line-height: 30px;
		border-top-left-radius: 15px;
		border-bottom-left-radius: 15px;
		border-top-right-radius: 15px;
		border-bottom-right-radius: 15px;
		font-size: 12px;
		color: #9b9ba3;
		background: rgb(239, 239, 244);
	}
}

.history {
	width: 100%;
	
	.firstLine {
		display: flex;
		margin-bottom: 14px;
		height: 30px;

		h3 {
			display: inline-block;
			flex: 1;
			font-size: 22px;
			color: #6b6b6b;
		}

		.delete {
			position: relative;
			padding-right: 20px;
			flex: 0 0 48px;
			width: 48px;
			height: 30px;
			line-height: 30px;
			text-align: center;
		}

		.delete span {
			display: inline-block;
			font-size: 12px;
			border: none !important; 
			color: #8a8a8f;
		}

		.delete img {
			position: absolute;
			right: 2px;
			top: 50%;
			display: inline-block;
			width: 20px;
			transform: translateY(-40%);
		}
	}

	& > span {
			display: block;
			height: 50px;
			line-height: 50px;
			font-size: 17px;
			@include border-bottom(rgb(235, 235, 240));
			color: #9b9ba3;
		}
}

.hint {
	position: fixed;
	top: 115px;
	left: 0;
	padding: 0 15px;
	width: calc(100% - 30px);
	height: 100%;
	background: #fff;
	z-index: 100;
}

.hint div {
	display: block;
	position: relative;
	padding-left: 24px;
	height: 52px;
	line-height: 52px;

	img {
		position: absolute;
		top: 50%;
		left: 0px;
		width: 18px;
		transform: translateY(-50%);
	}

	span {
		font-size: 17px;
		color: #222;
	}
}
</style>
